import React, { useState } from 'react';
import './App.css';
import TextOutput from './TextOutput';
import TextInput from './TextInput';
import LanguageSelector from './LanguageSelector';

const App = () => {
  const [inputText, setInputText] = useState('');
  const [outputText, setOutputText] = useState('');
  const [detectedLanguage, setDetectedLanguage] = useState('');
  const [translatedText, setTranslatedText] = useState('');
  const [selectedLanguage, setSelectedLanguage] = useState('en');
  const [summary, setSummary] = useState('');
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  const detectLanguage = async (text) => {
    console.log('Detecting language for:', text); // Debug log
    try {
      /* eslint-disable no-undef */
      if (!chrome.ai || !chrome.ai.languageDetection) {
        throw new Error('Chrome Language Detection API is not available.');
      }
      const detectedLanguage = await chrome.ai.languageDetection.detectLanguage({ text });
      /* eslint-enable no-undef */
      console.log('Language detected:', detectedLanguage.language); // Debug log
      setDetectedLanguage(detectedLanguage.language || "Unknown");
    } catch (error) {
      console.error('Language detection failed:', error); // Debug log
      setError('Unable to detect language. Please try again.');
    }
  };

  const handleTranslate = async () => {
    console.log('Translating text:', outputText, selectedLanguage); // Debug log
    setIsLoading(true);
    try {
      /* eslint-disable no-undef */
      if (!chrome.ai || !chrome.ai.translator) {
        throw new Error('Chrome Translator API is not available.');
      }
      const translation = await chrome.ai.translator.translate({
        text: outputText,
        targetLang: selectedLanguage,
      });
      /* eslint-enable no-undef */
      console.log('Translation result:', translation.translatedText); // Debug log
      setTranslatedText(translation.translatedText || "Translation failed.");
      setError(null);
    } catch (error) {
      console.error('Translation failed:', error); // Debug log
      setError('Unable to translate the text. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  const handleSummarize = async () => {
    if (!outputText.trim() || detectedLanguage !== 'en' || outputText.length <= 150) {
      setError('Summarization is only available for English text longer than 150 characters.');
      return;
    }

    setIsLoading(true);
    try {
      const summarizedText = await summarizeText(outputText);
      setSummary(summarizedText);
      setError(null);
    } catch (error) {
      console.error('Summarization failed:', error);
      setError('Unable to summarize the text. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  const summarizeText = async (text) => {
    try {
      /* eslint-disable no-undef */
      if (!chrome.ai || !chrome.ai.summarizer) {
        throw new Error('Chrome Summarizer API is not available.');
      }
      const response = await chrome.ai.summarizer.summarize({ text });
      /* eslint-enable no-undef */
      return response.summary;
    } catch (error) {
      console.error('Summarization failed:', error);
      throw error;
    }
  };

  const handleSend = async () => {
    if (!inputText.trim()) return;
    setOutputText(inputText);
    setInputText("");
    setSummary("");
    await detectLanguage(inputText); // Detect language after setting the output text
  };

  return (
    <div className="app">
      <div className="chat-container">
        <div className="output-area">
          <TextOutput text={outputText} label="Output" />
          <TextOutput text={detectedLanguage} label="Detected Language" />
          {detectedLanguage === 'en' && outputText.length > 150 && (
            <button onClick={handleSummarize} disabled={isLoading}>
              {isLoading ? 'Summarizing...' : 'Summarize'}
            </button>
          )}
          {summary && <TextOutput text={summary} label="Summary" />}
          <LanguageSelector selectedLanguage={selectedLanguage} onChange={(e) => setSelectedLanguage(e.target.value)} />
          <button onClick={handleTranslate} disabled={isLoading}>
            {isLoading ? 'Translating...' : 'Translate'}
          </button>
          <TextOutput text={translatedText} label="Translation" />
          {error && <p className="error">Error: {error}</p>}
        </div>
        <TextInput value={inputText} onChange={(e) => setInputText(e.target.value)} onSend={handleSend} />
      </div>
    </div>
  );
};

export default App;